<template>
  <div>
    <v-container>
      <v-row>
        <v-col align="center">
          <h2>สถานะ : {{ status_title }}</h2>
          <v-img lazy-src="../assets/img/lock.png" max-height="250" max-width="250" src="../assets/img/lock.png" v-if="icon_lock" ></v-img>
           <v-img lazy-src="../assets/img/unlock.png"  max-height="250" max-width="250" src="../assets/img/unlock.png" v-if="icon_unlock" ></v-img>
          <v-btn color="primary" v-if="btn" elevation="6" @click="changStatus()">ต้องการปลดล็อค</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
    data() {
        return {
            status_title:"",
            status_lock:null,
            icon_lock:false,
            icon_unlock:false,
            btn:true,
            header1: { headers: { Authorization: "Device 9be0df5a-da0c-4428-b5bd-991b35f5b1bc:vEB1ratSCXyVDDjeDt1seBvaPrK5BGiV" } },
            header2: { headers: { Authorization: "Device ad0f186d-4eea-4105-a1a2-c9a0e4cad0ff:EqfgqPoppB9SdYwxVCiCdzdMmzp1V9rm" } }
        }
    },
    created(){
        this.getdata()
    },
    methods: {
        changStatus(){
            console.log("send "+this.status_lock)
            this.axios.put("https://api.netpie.io/v2/device/message/set/status",this.status_lock,this.header2).
           then((response) => {
            console.log(response);
            setTimeout(() => {
              console.log("timeout1")
              this.getdata()
            }, 500);

            setTimeout(() => {
              console.log("timeout2")
              this.getdata()
            }, 5500);
          }).catch((err) => {
            console.log(err);
            this.btn = false
            this.icon_lock = false
            this.icon_unlock = false
            this.status_title = err
            this.swalAlertError()
            });
        },
        getdata(){
            this.axios.get("https://api.netpie.io/v2/device/shadow/data", this.header1)
            .then((response) => {
              // console.log(response);
              let check_status_lock = response.data.data.lock_state
              console.log(check_status_lock);
              if(check_status_lock == 0){
                this.status_title = "ล็อค"
                this.btn = true
                this.status_lock = 1
                this.icon_lock = true
                this.icon_unlock = false
                this.swalAlertSuccess("ล็อคเรียบร้อยแล้ว")
              }
              if(check_status_lock == 1){
                this.status_title = "ไม่ล็อค"
                this.btn = false
                this.status_lock = 0
                this.icon_unlock = true
                this.icon_lock = false
                this.swalAlertSuccess("ปลดล็อคเรียบร้อยแล้ว")
              }        
            }).catch((err) => {
              console.log(err);
                this.btn = false
                this.status_title = err
                this.swalAlertError()
            });
            },
        swalAlertSuccess(mag){
            this.$swal.fire({
            icon: "success",
            title: mag,
            showConfirmButton: false,
            timer: 1500,
            showCloseButton: true,
          });
        },
        swalAlertError(){
            this.$swal.fire({
            icon: "error",
            width: 500,
            title: "เกิดข้อผิดพลาด",
            showConfirmButton: false,
            timer: 1500,
            showCloseButton: true,
          });
        }
    }
}
</script>
